  top: 0;
  left: 0;
  width: 100%;
  height: 70%;
  content: "с кем работали";
  transform: rotateX(15deg) translateZ(-5px);
}
.partners__content {
  height: 100%;
}

.partners-slide__body {
  display: flex;
}
.partners-slide__column {
  border-radius: 10px;
}
.partners-slide__content {
  display: grid;
  overflow: hidden;
  height: 100%;
  border-radius: 10px;
  grid-template-columns: minmax(150px, min-content) auto;
}
@media (max-width: 690px) {
  .partners-slide__content {
    grid-template-columns: minmax(150px, 1fr) !important;
  }
}
.partners-slide__image {
  width: 100%;
  grid-row: 1/3;
}
@media (max-width: 768px) {
  .partners-slide__image {
    grid-row: 1/2;
  }
}
@media (max-width: 690px) {
  .partners-slide__image {
    grid-column: 1/1 !important;
  }
}
.partners-slide__image img {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 50px;
  border-bottom-left-radius: 10px;
  background-size: cover;
  object-fit: cover;
}
.partners-slide__title {
  font-size: 2rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 1.5rem;
  grid-column: 2/3;
}
@media (max-width: 768px) {
  .partners-slide__title {
    grid-row: -1/-1;
  }
}
@media (max-width: 690px) {
  .partners-slide__title {
    text-align: left;
    grid-row: 2/3 !important;
    grid-column: 1/1;
  }
}
.partners-slide__text {
  font-size: 1.4rem;
  height: 100%;
  padding: 1.5rem;
  text-align: left;
}
@media (max-width: 768px) {
  .partners-slide__text {
    grid-row: 2/3;
    grid-column: 1/3;
  }
}
@media (max-width: 690px) {
  .partners-slide__text {
    grid-row: 3/4 !important;
  }
}
.swiper-wrapper {
  align-items: flex-start;
}